
import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import RaisedButton from 'material-ui/RaisedButton';
import { connect } from 'react-redux';

import s from './Reset.css';
import FormComponent from '../../../components/FormComponent';
import { reset } from '../../../actions';

class Reset extends FormComponent {
  constructor(props) {
    super(props);
    this.state = {
      email: '',
    };
    this.requiredFields = ['email'];
  }

  onReset = () => {
    if (!this.valid()) {
      return;
    }
    this.props.reset(this.state.email);
  }

  render() {
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1>Reset Password</h1>
          {
            this.renderTextField({
              id: 'email',
              hint: 'Email Address',
              style: { width: 300 },
            })
          }
          <br />
          <RaisedButton
            label="Reset"
            primary
            fullWidth
            className={s.resetButton}
            onTouchTap={this.onReset}
          />
        </div>

      </div>
    );
  }
}

Reset.propTypes = {
  reset: PropTypes.func.isRequired,
};

Reset.defaultProps = {
};

const mapState = state => ({
  ...FormComponent.mapState(state),
});

const mapDispatch = {
  reset,
  ...FormComponent.mapDispatch,
};

export default connect(mapState, mapDispatch)(withStyles(s)(Reset));



