/* eslint-disable import/prefer-default-export */

import history from '../history';
import { getUserType, setUserType } from '../data/shared';
import * as C from '../data/const';
import * as g from './global';
import * as api from '../data/api';

const getData = (dispatch, userType) => {
  api.getUser().then((user) => {
    dispatch(g.setNone());
    if (user.businesses.length) {
      history.push('/recruiter/find');
    } else if (user.job_seeker) {
      history.push('/recruiter/find');
    } else {
      const type = userType || getUserType();
      if (type === C.UT_RECRUITER) {
        history.push('/recruiter/find');
      } else if (type === C.UT_JOBSEEKER) {
        history.push('/recruiter/find');
      } else {
        dispatch({ type: C.SELECT_TYPE });
      }
    }
  })
  .catch(errors => dispatch(g.setAPIErrors(errors)));
};

export const loginWithType = (type) => {
  setUserType(type);
  if (type === C.UT_RECRUITER) {
    history.push('/recruiter/find');
  } else if (type === C.UT_JOBSEEKER) {
    history.push('/recruiter/find');
  }
};

export const login = (email, password) => (dispatch) => {
  dispatch(g.setLoading());
  api.login({ email, password })
    .then(() => getData(dispatch))
    .catch(errors => dispatch(g.setAPIErrors(errors)));
};

export const register = (email, password1, password2, type) => (dispatch) => {
  dispatch(g.setLoading());
  api.register({ email, password1, password2 })
    .then(() => getData(dispatch, type))
    .catch(errors => dispatch(g.setAPIErrors(errors)));
};

export const reset = email => (dispatch) => {
  dispatch(g.setLoading());
  api.resetPassowrd({ email })
    .then(() => dispatch(g.setAlert('Success')))
    .catch(errors => dispatch(g.setAPIErrors(errors)));
};

