
import React from 'react';
import HomeLayout from '../../../components/HomeLayout';
import Reset from './Reset';

const title = 'Reset Password';

export default {

  path: '/reset',
  redirectIfAuth: true,

  action() {
    return {
      title,
      component: <HomeLayout><Reset /></HomeLayout>,
    };
  },

};



