
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Businesses from './Businesses';

export default {

  path: '/recruiter/businesses',

  async action() {
    return {
      title: 'Business List',
      component: <MainLayout path={this.path}><Businesses /></MainLayout>,
    };
  },

};



