
import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import RaisedButton from 'material-ui/RaisedButton';
import Dialog from 'material-ui/Dialog';
import { connect } from 'react-redux';

import s from './Login.css';
import Link from '../../../components/Link';
import FormComponent from '../../../components/FormComponent';
import { login, loginWithType } from '../../../actions';
import * as C from '../../../data/const';

class Login extends FormComponent {

  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password: '',
    };
    this.requiredFields = ['email', 'password'];
  }

  onLogin = () => {
    if (!this.valid()) {
      return;
    }
    const { email, password } = this.state;
    this.props.login(email, password);
  }

  render() {
    const tfStyle = { width: 300 };
    return (
      <div className={s.root}>

        <div className={s.container}>
          <h1>Login</h1>
          {
            this.renderTextField({
              id: 'email',
              hint: 'Email Address',
              style: tfStyle,
            })
          }
          {
            this.renderTextField({
              type: 'password',
              id: 'password',
              hint: 'Password',
              style: tfStyle,
            })
          }
          <br />
          {
            this.renderCheckbox({
              id: 'remember',
              label: 'Remember password',
            })
          }
          <RaisedButton
            label="Login"
            primary
            fullWidth
            className={s.loginButton}
            onTouchTap={this.onLogin}
          />

          <Link to="/reset" className={s.reset}>Forgot password?</Link>

        </div>

        <Dialog
          title="Select Type"
          modal
          open={this.props.selectType}
          contentStyle={{ maxWidth: '350px' }}
        >
          <RaisedButton
            label="Recruiter"
            primary
            fullWidth
            style={{ marginBottom: '15px' }}
            onTouchTap={() => loginWithType(C.UT_RECRUITER)}
          />
          <RaisedButton
            label="JobSeeker"
            secondary
            fullWidth
            onTouchTap={() => loginWithType(C.UT_JOBSEEKER)}
          />
        </Dialog>

      </div>
    );
  }
}

Login.propTypes = {
  selectType: PropTypes.bool,
  login: PropTypes.func.isRequired,
};

Login.defaultProps = {
  selectType: false,
};

const mapState = state => ({
  selectType: state.auth.selectType,
  ...FormComponent.mapState(state),
});

const mapDispatch = {
  login,
  ...FormComponent.mapDispatch,
};

export default connect(mapState, mapDispatch)(withStyles(s)(Login));



