
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Connections from './Connections';

export default {

  path: '/recruiter/connections',

  async action() {
    return {
      title: 'Connections',
      component: <MainLayout path={this.path}><Connections /></MainLayout>,
    };
  },

};



