
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Messages from './Messages';

export default {

  path: '/recruiter/messages',

  async action() {
    return {
      title: 'Messages',
      component: <MainLayout path={this.path}><Messages /></MainLayout>,
    };
  },

};



