
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Password from './Password';

export default {

  path: '/recruiter/password',

  async action() {
    return {
      title: 'Change Password',
      component: <MainLayout path={this.path}><Password /></MainLayout>,
    };
  },

};



