
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Help from './Help';

export default {

  path: '/recruiter/help',

  async action() {
    return {
      title: 'Help',
      component: <MainLayout path={this.path}><Help /></MainLayout>,
    };
  },

};



