
import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import RaisedButton from 'material-ui/RaisedButton';
import { connect } from 'react-redux';

import s from './Register.css';
import FormComponent from '../../../components/FormComponent';
import { register } from '../../../actions';
import * as C from '../../../data/const';

const TITLES = {
  [C.UT_RECRUITER]: 'Recruiter Registration',
  [C.UT_JOBSEEKER]: 'JobSeeker Registration',
};

class Register extends FormComponent {

  constructor(props) {
    super(props);
    this.state = {
      email: '',
      password1: '',
      password2: '',
    };
    this.requiredFields = ['email', 'password1', 'password2'];
  }

  onRegister = () => {
    if (!this.valid()) {
      return;
    }
    const { email, password1, password2 } = this.state;
    this.props.register(email, password1, password2, this.props.type);
  }

  render() {
    const tfStyle = { width: 300 };
    return (
      <div className={s.root}>
        <div className={s.container}>
          <h1>{TITLES[this.props.type]}</h1>
          {
            this.renderTextField({
              id: 'email',
              hint: 'Email Address',
              style: tfStyle,
            })
          }
          {
            this.renderTextField({
              type: 'password',
              id: 'password1',
              hint: 'Password',
              style: tfStyle,
            })
          }
          {
            this.renderTextField({
              type: 'password',
              id: 'password2',
              hint: 'Confirm Password',
              error: this.state.error,
              style: tfStyle,
            })
          }
          <br />
          {
            this.renderCheckbox({
              id: 'remember',
              label: 'Remember password',
            })
          }
          <RaisedButton
            label="Register"
            primary
            fullWidth
            className={s.registerButton}
            onTouchTap={this.onRegister}
          />
        </div>

      </div>
    );
  }
}

Register.propTypes = {
  type: PropTypes.string.isRequired,
  register: PropTypes.func.isRequired,
};

Register.defaultProps = {
};

const mapState = state => ({
  ...FormComponent.mapState(state),
});

const mapDispatch = {
  register,
  ...FormComponent.mapDispatch,
};

export default connect(mapState, mapDispatch)(withStyles(s)(Register));



