/* eslint-disable global-require */
export default {

  path: '/',

  // Keep in mind, routes are evaluated in order
  children: [
    require('./home/landing').default,
    require('./home/jobseeker').default,
    require('./home/recruiter').default,
    require('./home/about').default,
    require('./home/terms').default,
    require('./home/login').default,
    require('./home/register').default,
    require('./home/reset').default,
    require('./main/recruiter/applications').default,
    require('./main/recruiter/businesses').default,
    require('./main/recruiter/connections').default,
    require('./main/recruiter/credit').default,
    require('./main/recruiter/findTalent').default,
    require('./main/recruiter/help').default,
    require('./main/recruiter/messages').default,
    require('./main/recruiter/password').default,
    require('./main/recruiter/shortlist').default,

    // Wildcard routes, e.g. { path: '*', ... } (must go last)
    require('./notFound').default,
  ],

  async action({ next }) {
    // Execute each child route until one of them return the result
    const route = await next();

    // Provide default values for title, description etc.
    route.title = `${route.title || 'Untitled Page'} - www.sclabs.co.uk`;
    route.description = route.description || '';

    return route;
  },

};



