
import React from 'react';
import HomeLayout from '../../../components/HomeLayout';
import Register from './Register';

const title = 'Registration';

export default {

  path: '/register/:type',
  redirectIfAuth: true,

  action({ params }) {
    return {
      title,
      component: <HomeLayout><Register type={params.type} /></HomeLayout>,
    };
  },

};



