
import React from 'react';
import Landing from './Landing';
import HomeLayout from '../../../components/HomeLayout';

export default {

  path: '/',
  redirectIfAuth: true,

  async action() {
    return {
      title: 'My Job Pitch',
      component: <HomeLayout><Landing /></HomeLayout>,
    };
  },

};



