
import React from 'react';
import PropTypes from 'prop-types';
import Paper from 'material-ui/Paper';
import FlatButton from 'material-ui/FlatButton';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { connect } from 'react-redux';

import s from './Businesses.css';
import m from '../../main.css';
import BaseComponent from '../../../../components/BaseComponent';
import { getBusinesses } from '../../../../actions';

const TestData = [
  1, 2, 3, 4,
];

class Businesses extends BaseComponent {

  // constructor(props) {
  //   super(props);
  //   // this.onRefresh();
  // }

  onAdd = () => {

  }

  onRefresh = () => {
    // this.props.getBusinesses();
  }

  render() {
    return (
      <div>
        <Paper zDepth={1} rounded={false} >
          <div className={m.header}>
            <div className={m.title}>Businesses</div>
            <FlatButton
              label="ADD"
              primary
              icon={<i className="fa fa-plus" />}
              onTouchTap={this.onAdd}
            />
            <FlatButton
              label="REFRESH"
              primary
              icon={<i className="fa fa-refresh" />}
              onTouchTap={this.onRefresh}
            />
          </div>
          <div className={m.content}>
            {
              this.props.loading ?
              ''
              :
              TestData.map(item => (
                <div
                  key={item}
                  className={s.businessItem}
                >
                  <div
                    style={{ backgroundImage: "url('https://www.sclabs.co.uk/media/location/2017/05/15/photo_thumbnail.jpg')" }}
                    className={s.logo}
                  />
                  <div className={s.name}>
                    Name
                  </div>

                  <div className={s.credits}>
                    87 Credits
                  </div>

                  <FlatButton
                    label="Includes 5 work places"
                    onTouchTap={this.onRefresh}
                    className={s.workPlaces}
                  />

                  <FlatButton
                    label="EDIT"
                    secondary
                    icon={<i className="fa fa-pencil" />}
                    onTouchTap={this.onRefresh}
                  />
                  <FlatButton
                    label="DELETE"
                    secondary
                    icon={<i className="fa fa-trash-o" />}
                    onTouchTap={this.onRefresh}
                  />
                </div>
              ))
            }
          </div>

        </Paper>
      </div>
    );
  }
}

Businesses.propTypes = {
  // loading: PropTypes.bool,
  getBusinesses: PropTypes.func.isRequired,
};

Businesses.defaultProps = {
  // loading: false,
};

const mapState = state => ({
  ...BaseComponent.mapState(state),
  // loading: state.global.loading,
});

const mapDispatch = {
  ...BaseComponent.mapDispatch,
  getBusinesses,
};

export default connect(mapState, mapDispatch)(withStyles(m, s)(Businesses));



