
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Credit from './Credit';

export default {

  path: '/recruiter/credit',

  async action() {
    return {
      title: 'Credit',
      component: <MainLayout path={this.path}><Credit /></MainLayout>,
    };
  },

};



