
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import FindTalent from './FindTalent';

export default {

  path: '/recruiter/find',

  async action() {
    return {
      title: 'Find Talent',
      component: <MainLayout path={this.path}><FindTalent /></MainLayout>,
    };
  },

};



