
import React from 'react';
import HomeLayout from '../../../components/HomeLayout';
import Login from './Login';

const title = 'Login';

export default {

  path: '/login',
  redirectIfAuth: true,

  action() {
    return {
      title,
      component: <HomeLayout><Login /></HomeLayout>,
    };
  },

};



