/* eslint-disable import/prefer-default-export */

import axios from 'axios';
import { API_BASE_URL } from './const';
import * as shared from './shared';

axios.defaults.baseURL = API_BASE_URL;
axios.defaults.headers.common.Accept = 'application/json';
axios.defaults.headers.post['Content-Type'] = 'application/json';

const getUrl = path => `/api/${path}/`;

export const getData =
  url => axios.get(url)
  .then(response => Promise.resolve(response.data))
  .catch((error) => {
    const response = error.response;
    return Promise.reject(response.status === 500 ? response.statusText : response.data);
  });

export const getDataByPath =
  path => getData(getUrl(path));

export const postData =
  (url, data) => axios.post(url, data)
  .then(response => Promise.resolve(response.data))
  .catch((error) => {
    const response = error.response;
    return Promise.reject(response.status === 500 ? response.statusText : response.data);
  });

export const postDataByPath =
  (path, data) => postData(getUrl(path), data);

export const allData =
  (promises, callback) => axios.all(promises)
  .then(axios.spread(callback));

const setToken = (token) => {
  shared.setToken(token);
  axios.defaults.headers.common.Authorization = `Token ${token}`;
};

export const logout = () => {
  shared.setToken();
};

export const register = info => postData('/api-rest-auth/registration/', info)
  .then((data) => {
    setToken(data.key);
    return Promise.resolve();
  });

export const login = info => postData('/api-rest-auth/login/', info)
  .then((data) => {
    setToken(data.key);
    return Promise.resolve();
  });

export const resetPassowrd = info => postData('/api-rest-auth/password/reset/', info);

export const getUser = () => getData('/api-rest-auth/user/');

export const getBusinesses = () => getData('/api/user-businesses/');



