
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import Applications from './Applications';

export default {

  path: '/recruiter/applications',

  async action() {
    return {
      title: 'Applications',
      component: <MainLayout path={this.path}><Applications /></MainLayout>,
    };
  },

};



