
import React from 'react';
import HomeLayout from '../../../components/HomeLayout';
import Page from '../../../components/Page';

export default {

  path: '/terms',
  redirectIfAuth: true,

  async action() {
    const data = await require.ensure([], require => require('./terms.md'), 'terms');

    return {
      title: data.title,
      component: <HomeLayout><Page {...data} /></HomeLayout>,
    };
  },

};



