
import React from 'react';
import HomeLayout from '../../../components/HomeLayout';
import Page from '../../../components/Page';

export default {

  path: '/jobseeker',
  redirectIfAuth: true,

  async action() {
    const data = await require.ensure([], require => require('./jobseeker.md'), 'jobseeker');

    return {
      title: data.title,
      component: <HomeLayout><Page {...data} /></HomeLayout>,
    };
  },

};



