
import React from 'react';
import PropTypes from 'prop-types';
import IconButton from 'material-ui/IconButton';
import FlatButton from 'material-ui/FlatButton';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import s from './MainLayout.css';

import TitleImgUrl from '../../assets/images/title.png';
import history from '../../history';

const recruiterMenus = [
  { id: 1, label: 'Find Talent', icon: 'fa-search', to: '/recruiter/find' },
  { id: 2, label: 'Applications', icon: 'fa-id-badge', to: '/recruiter/applications' },
  { id: 3, label: 'Connections', icon: 'fa-handshake-o', to: '/recruiter/connections' },
  { id: 4, label: 'My Shortlist', icon: 'fa-tags', to: '/recruiter/shortlist' },
  { id: 5, label: 'Messages', icon: 'fa-envelope-o', to: '/recruiter/messages' },
  { id: 6, label: 'Add or Edit Jobs', icon: 'fa-briefcase', to: '/recruiter/businesses' },
  { id: 7, label: 'Credit', icon: 'fa-credit-card', to: '/recruiter/credit' },
  { id: 8, label: 'Change Password', icon: 'fa-key', to: '/recruiter/password' },
  { id: 9, label: 'Help', icon: 'fa-question-circle-o', to: '/recruiter/help' },
];

class MainLayout extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      small: true,
      open: false,
    };
  }

  componentDidMount() {
    window.addEventListener('resize', this.updatedDimensions);
    this.updatedDimensions();
  }

  componentWillUnmount() {
    window.removeEventListener('resize', this.updatedDimensions);
  }

  onToggleSidebar = () => this.setState({ open: !this.state.open });

  onCloseSidebar = () => this.setState({ open: false });

  onLogout = () => {
    history.push('/');
  }

  onClickItem = (to) => {
    history.push(to);
  }

  updatedDimensions = () => {
    const small = window.innerWidth < 768;
    if (small !== this.state.small) {
      this.setState({
        small,
        open: !small,
      });
    }
  }

  render() {
    const { path, children } = this.props;
    const { small, open } = this.state;
    return (
      <div className={`${s.root} ${small ? s.small : ''} ${open ? s.open : ''}`}>
        <header className={s.header}>
          {
            small ?
              <IconButton
                iconClassName={`fa fa-bars ${s.toggleIcon}`}
                onTouchTap={this.onToggleSidebar}
              /> : ''
          }
          <div className={s.title}>
            <img src={TitleImgUrl} alt="" />
          </div>
          <IconButton
            iconClassName={`fa fa-sign-out ${s.logoutIcon}`}
            onTouchTap={this.onLogout}
          />
        </header>
        <div className={s.container}>
          <div className={s.sidebar} >
            <nav>
              <ul>
                {
                  recruiterMenus.map(item => (
                    <li key={item.id}>
                      <FlatButton
                        icon={<i className={`fa ${item.icon}`} />}
                        label={item.label}
                        rippleColor="#222"
                        hoverColor="#2d2d2d"
                        className={item.to === path ? s.active : ''}
                        onTouchTap={() => this.onClickItem(item.to)}
                      />
                    </li>
                  ))
                }
              </ul>
            </nav>
            <div className={s.footer}>
              <div>@ 2017 Sclabs Inc</div>
            </div>
          </div>
          <div className={s.content}>
            {children}
          </div>
          {
            small ?
              <div
                className={s.mask}
                onTouchTap={this.onCloseSidebar}
              /> : ''
          }
        </div>
      </div>
    );
  }
}

MainLayout.propTypes = {
  path: PropTypes.string.isRequired,
  children: PropTypes.node.isRequired,
};

export default withStyles(s)(MainLayout);



