import * as C from '../data/const';

const initialStates = {
  gs: C.GS_NONE,
  data: null,
};

export default function global(state = initialStates, action) {
  switch (action.type) {
    case C.SET_GLOBAL_STETE:
      return {
        gs: action.gs,
        gsData: action.data,
      };
    default:
      return state;
  }
}



