/* eslint-disable import/prefer-default-export */

import * as C from '../data/const';

const setGlobalState = (gs, data) => ({
  type: C.SET_GLOBAL_STETE,
  gs,
  data,
});

export const setNone = () => setGlobalState(C.GS_NONE, null);
export const setLoading = () => setGlobalState(C.GS_LOADING, null);
export const setError = data => setGlobalState(C.GS_ERROR, data);
export const setWarning = data => setGlobalState(C.GS_WARNING, data);
export const setAlert = data => setGlobalState(C.GS_ALERT, data);
export const setAPIErrors = data => setGlobalState(C.GS_API_ERROR, data);



