
import React from 'react';
import MainLayout from '../../../../components/MainLayout';
import ShortList from './ShortList';

export default {

  path: '/recruiter/shortlist',

  async action() {
    return {
      title: 'ShortList',
      component: <MainLayout path={this.path}><ShortList /></MainLayout>,
    };
  },

};



